@import url(https://fonts.googleapis.com/css2?family=Staatliches&display=swap);
*{
  padding: 0;
  margin: 0;
  list-style: none;
  text-decoration: none;
  font-family: 'M PLUS Rounded 1c', sans-serif;
}

::-webkit-scrollbar{
  background-color: #1078ce;
}
:root{
    --blue: #0064b6;
    --light_blue: #1078ce;
    --dark_blue: #014985;
    --white_blue: #7bb1dd;
}

.set_users_go_right{
    position: absolute;
    top: 70px;
    right: -100px;
    opacity: 0;
    background: #1078ce;
    background: var(--light_blue);
    color: white;
    cursor: pointer;
    padding: 10px;
    border-radius: 50px 10px 10px 50px;
    font-size: 15px;
}

.main_app{
    display: flex;
    background: pink;
    margin: 30px auto 0 auto;
    width: 55%;
    height: 660px;
    border-radius: 10px;
    box-shadow: 0 3px 15px rgb(0, 0, 0);
}

/* Users Part 1 */
.users{
    flex: .3 1;
    background: #0064b6;
    background: var(--blue);
    max-width: 100%;
    padding-top: 15px;
    border-radius: 10px 0 0 10px;
}
.me{
    color: white;
    background-color: #7bb1dd;
    background-color: var(--white_blue);
    padding-left: 20px;
    font-weight: 500;
    border-radius: 20px 20px 0 0;
    cursor: pointer;
}
.users_list{
    background-color: #1078ce;
    background-color: var(--light_blue);
    max-width: 100%;
    height: 90%;
    overflow: hidden;
    overflow-y: scroll;
}
.user{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding: 10px;
    background: #1078ce;
    background: var(--light_blue);
    border-bottom: 1px solid #0064b6;
    border-bottom: 1px solid var(--blue);

    transition: all .3s;
}
.user:hover{
    cursor: pointer;
    background: #7bb1dd;
    background: var(--white_blue);
}
.user:active {
    background: #014985;
    background: var(--dark_blue);
}
.user .avatar{
    margin-right: 10px;
}
.flex{
    display: flex;
    flex-direction: column;
}
.user p{
    color: white;
    font-weight: 600;
}
.isWriting{
    color: rgba(200, 200, 200, 0.9);
}

/* Room Part 2*/
.room{
    flex: .7 1;
    display: flex;
    flex-direction: column;
    background: green;
    width: 100%;
    border-radius: 0 10px 10px 0;
}

.room_header{
    flex: .1 1;
    display: flex;
    align-items: center;
    justify-content: center;
    /* background-color: var(--dark_blue); */
    background-color: white;
    border-bottom: 1px solid #014985;
    border-bottom: 1px solid var(--dark_blue);
    border-radius: 0 10px 0 0;
    color: #014985;
    color: var(--dark_blue);
    font-weight: 500;
    padding: 5px;
}
.room_header .avatar{
    margin-right: 10px;
}
.connected{
    width: 10px;
    height: 10px;
    margin-right: 5px;
    background-color: rgb(1, 230, 1);
    border-radius: 50px;
}

/* body */
.room_body{
    flex: .8 1;
    background-color: rgb(232, 247, 252);
    /* background-color: #95b3c7; */
    overflow: hidden;
    overflow-y: scroll;
}
.room_body::-webkit-scrollbar{
    background-color: transparent;
}

.message{
    position: relative;
    /* background: rgb(224, 224, 224); */
    background: white;
    border: 1px #014985 solid;
    border: 1px var(--dark_blue) solid;
    border-radius: 0 10px 10px 10px;
    margin: 10px;
    width: 40%;
    padding: 8px;
    box-shadow: -1px 3px 10px rgb(60, 60, 60);
    opacity: 0;

    -webkit-animation: anim_message 1s;

            animation: anim_message 1s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}
@-webkit-keyframes anim_message{
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}
@keyframes anim_message{
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}
.msg_mine{
    /* background: var(--white_blue); */
    color: white;
    background-color: #014985;
    background-color: var(--dark_blue);
    border: 1px white solid;
    border-radius: 10px 0 10px 10px;
    margin-left: 55%;
}

.message_user{
    font-weight: 700;
    color: #7bb1dd;
    color: var(--white_blue);
}
.time{
    font-weight: 400;
    color: #7bb1dd;
    color: var(--white_blue);
}

.msg_is_viewed{
    position: absolute;
    right: 10px;
    bottom: 1px;
    color: rgba(255, 255, 255, 0.2);
}
.msg_viewed{
    color: #70fcf5;
}

/* footer */
.room_footer{
    flex: .1 1;
    display: flex;
    background-color: #014985;
    background-color: var(--dark_blue);
    width: 100%;
    border-radius: 0 0 10px 0;
}
.room_footer input{
    margin: auto;
    width: 70%;
    padding: 5px;
    color: white;
    font-weight: 500;
    background-color: #0064b6;
    background-color: var(--blue);
    border: none;
    border-bottom: 1px solid #7bb1dd;
    border-bottom: 1px solid var(--white_blue);
    border-radius: 10px 5px;
    outline: none;

    transition: all .4s;
}
.room_footer input:focus{
    border-bottom: 4px solid #7bb1dd;
    border-bottom: 4px solid var(--white_blue);
}
.room_footer input::-webkit-input-placeholder{
    color: white;
}
.room_footer input:-ms-input-placeholder{
    color: white;
}
.room_footer input::-ms-input-placeholder{
    color: white;
}
.room_footer input::placeholder{
    color: white;
}
.room_footer button{
    background-color: #1078ce;
    background-color: var(--light_blue);
    border: none;
    padding: 0 10px;
    border-radius: 0 0 10px 0;
    color: white;
    font-weight: 600;
    outline: none;
}
.room_footer button:hover{
    cursor: pointer;
    background: #0064b6;
    background: var(--blue);
}
.room_footer button:active{
    background: #014985;
    background: var(--dark_blue);
}

/* media */
@media(max-width: 900px){
    .set_users_go_right{
        right: 5px;
        opacity: 1;
        z-index: 20;
    }

    .main_app{
        display: block;
        margin: auto;
        width: 100%;
        height: 100vh;
    }

    /* Users Part 1 */
    .users{
        position: absolute;
        top: 60px;
        left: -1000px;
        width: 50%;
        padding-top: 8px;
        z-index: 20;
        border-radius: 0 10px 10px 0;

        transition: all .5s;
    }
    .users_go_right{
        left: 0;
    }
    .users_list{
        max-height: 200px;
        font-size: 12px;
        overflow: hidden;
        overflow-y: scroll;
    }
    .flex{
        display: flex;
        flex-direction: column;
        max-width: 40px;
    }
    /* Room Part 2*/
    .room{
        height: 100%;
    }

    .room_header{
        flex: .07 1;
        font-weight: 600;
        font-size: 12px;
    }

    /* body */
    .room_body{
        flex: .86 1;
    }
    .message{
        font-size: 11px;
    }

    /* footer */
    .room_footer{
        flex: .07 1;
    }
    .room_footer input{
        width: 75%;
        padding: 5px;
        font-size: 12px;
    }
    .room_footer button{
        padding: 0 10px;
        font-size: 12px;
    }
}
:root{
    --blue: #0064b6;
    --light_blue: #1078ce;
    --dark_blue: #014985;
    --white_blue: #7bb1dd;
}

.alert_message{
    position: absolute;
    top: -60px;
    right: 60px;
    background: #1078ce;
    background: var(--light_blue);
    color: white;
    font-weight: 600;
    font-size: 20px;
    padding: 8px 12px;
    border-radius: 10px;
    box-shadow: 0 3px 10px rgb(85, 85, 85);
    z-index: 21;

    transition: all 1s;
}
.alert_message_go_down{
    top: 25px;
}

.error{
    background: #ce1010;
}
:root{
    --blue: #0064b6;
    --light_blue: #1078ce;
    --dark_blue: #014985;
    --white_blue: #7bb1dd;
}

.unseen_msg_notify{
    position: absolute;
    top: -100px;
    left: 40px;
    background: radial-gradient(#0a178a, #64b0ee);
    color: white;
    font-weight: 700;
    padding: 11px;
    border-radius: 50px;
    box-shadow: 0 2px 8px #0a178a;
    z-index: 21;

    transition: all .5s;
}
.unseen_msg_notify:hover{
    box-shadow: 0 4px 14px #071168;
    cursor: pointer;
}
.unseen_msg_notify_go_down{
    top: 15px;
}

/* messages */
.unseen_msgs{
    position: absolute;
    top: 80px;
    left: -300px;
    background: radial-gradient(#0a178a, #64b0ee);
    padding: 5px;
    border-radius: 1px 8px 8px 8px;
    box-shadow: 0 4px 10px #071168;
    z-index: 21;

    transition: all .5s;
}
.unseen_msgs_go_right{
    left: 40px;
}

.unseen_msg{
    position: relative;
    background: #8bc8fa;
    padding: 6px;
    border-bottom: #0064b6 solid 3px;
    border-bottom: var(--blue) solid 3px;
    border-radius: 5px;
    box-shadow: 0 5px 15px #071168;

    transition: all .5s;
}
.unseen_msg:hover{
    cursor: pointer;
    background: #6b9dcc;
}

.unseen_msg_user{
    font-weight: 700;
}

.unseen_msg_content{
    width: 200px;
    overflow: hidden;
}

.unseen_msg_length{
    position: absolute;
    right: 10px;
    top: -5px;
    background: rgb(1, 196, 1);
    font-weight: 500;
    color: white;
    padding: 2px;
    padding-top: 0;
    border-radius: 0 0 10px 10px;
}

/* media */
@media(max-width: 900px){
    .unseen_msg_notify{
        left: 10px;
        font-size: 15px;
    }
    .unseen_msg_content{
        width: 120px;
    }
    .unseen_msgs_go_right{
        left: 10px;
    }
}
:root{
    --blue: #0064b6;
    --light_blue: #1078ce;
    --dark_blue: #014985;
    --white_blue: #7bb1dd;
}

body{
    background: whitesmoke;
}

.logsign{
    background: white;
    width: 40%;
    margin: 75px auto;
    text-align: center;
    border-radius: 20px;
    box-shadow: 0 3px 15px rgb(46, 46, 46);
}

.logsign .header{
    font-family: 'Staatliches', cursive;
    color: rgb(0, 100, 182);
    font-weight: 900;
    font-size: 70px;
    padding: 2rem;
}

/* Login register */
.logsign_container{
    width: 80%;
    margin: auto;
    padding: 1rem;
}

.logsign_container .login,
.logsign_container .register{
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 5px;

    transition: all 1s;
}

.hide_logsign{
    height: 0;
    opacity: 0;
    pointer-events: none;
    display: none;
}

.logsign_container .login input,
.logsign_container .register input{
    padding: 10px;
    margin: 10px 0;
    border-radius: 5px;
    border: 1px solid grey;
    outline: none;
    font-weight: 600;

    transition: all .4s;
}
.logsign_container .login input:focus,
.logsign_container .register input:focus{
    border: 2px solid #0064b6;
}

.logsign_container .login .btn,
.logsign_container .register .btn{
    display: block;
    width: 100px;
    margin: 10px auto;
    padding: 5px 3px;
    border-radius: 5px;
    background: #0064b6;
    color: white;
    font-weight: 600;
    font-size: 15px;
    border: none;
    outline: none;
}
.logsign_container .login .btn:hover,
.logsign_container .register .btn:hover{
    background: #1078ce;
    cursor: pointer;
}
.logsign_container .login .btn:active,
.logsign_container .register .btn:active{
    background: #014985;
}

.logsign_container .login .btn_loading,
.logsign_container .register .btn_loading{
    background: #7bb1dd;
    pointer-events: none;
}

/* switcher */
.logsign .switcher{
    display: inline-block;
    color: #0064b6;
    font-weight: 400;
    font-size: 18px;
    padding: 10px;
    padding-bottom: 20px;
    cursor: pointer;
}

/* media */
@media(max-width: 900px){
    .logsign{
        width: 80%;
        margin: 10px auto;
    }
    .logsign .header{
        font-size: 40px;
    }
    .logsign_container .login input,
    .logsign_container .register input{
        font-size: 15px;
    }
    .logsign_container .login .btn,
    .logsign_container .register .btn{
        width: 100px;
        padding: 8px 3px;
        border-radius: 15px;
        font-weight: 700;
        font-size: 15px;
    }

    /* switcher */
    .logsign .switcher{
        font-size: 15px;
    }
}
